.greetingForm {

}

.inputAndButtonContainer {
    display: flex;

}

.error {
    color: red;
}

.input {
    padding: 10px;
    border: 2px solid #E0E0E0;
    border-radius: 4px;
    width: calc(100% - 20px);
    box-sizing: border-box;
    margin-right: 10px;
    font-size: 16px;
}

.input:focus {

    outline: none;
}

.errorInput {
    border-color: red;
}

.button {
    background-color: #0065ca;
    border: 2px solid #0065ca;
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
    border-radius: 4px;
}

.button:hover {
    background-color: #005bb5;  /* –ù–µ–º–Ω–æ–≥–æ —Ç–µ–º–Ω–µ–µ, —á–µ–º –æ—Å–Ω–æ–≤–Ω–æ–π —Ü–≤–µ—Ç –∫–Ω–æ–ø–∫–∏ */
}

.button:focus {
    outline: none;
    border-color: #005bb5;  /* –°–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤–∏–µ —Ü–≤–µ—Ç–∞ –≥—Ä–∞–Ω–∏—Ü—ã —Ü–≤–µ—Ç—É –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
}

.button:active {
    background-color: #0050a0;  /* –ï—â–µ —Ç–µ–º–Ω–µ–µ –ø—Ä–∏ –∞–∫—Ç–∏–≤–∞—Ü–∏–∏ */
}

.button:disabled {
    background-color: #E0E0E0;
    border-color: #E0E0E0;
    cursor: not-allowed;
}

