/* –û—Å–Ω–æ–≤–Ω–æ–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è —Å–æ–æ–±—â–µ–Ω–∏—è –¥—Ä—É–≥–∞ */
.friendMessage {
    flex-direction: column;
    padding: 10px;
    margin: 5px 0;
    align-items: flex-start; /* –í—ã—Ä–∞–≤–Ω–∏–≤–∞–µ–º —Å–æ–æ–±—â–µ–Ω–∏–µ –¥—Ä—É–≥–∞ —Å–ª–µ–≤–∞ */
    position: relative;
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è –∏ —Ç–µ–∫—Å—Ç–∞ —Å–æ–æ–±—â–µ–Ω–∏—è –¥—Ä—É–≥–∞ */
.friendImageAndText {
    display: flex;
    align-items: center;
    flex-direction: row; /* –°—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ–µ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –¥–ª—è —Å–æ–æ–±—â–µ–Ω–∏—è –¥—Ä—É–≥–∞ */
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è –∞–≤–∞—Ç–∞—Ä–∞ –¥—Ä—É–≥–∞ */
.friendImageAndText img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    margin-left: 10px; /* –û—Ç—Å—Ç—É–ø —Å–ª–µ–≤–∞ –¥–ª—è –∞–≤–∞—Ç–∞—Ä–∞ –¥—Ä—É–≥–∞ */
    object-fit: cover;
    padding: 10px;
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è —Ç–µ–∫—Å—Ç–∞ —Å–æ–æ–±—â–µ–Ω–∏—è –∏ –∏–º–µ–Ω–∏ –¥—Ä—É–≥–∞ */
.friendText {
    position: relative;
    max-width: 80%;
    display: flex;
    flex-direction: column;
    background-color: #eee; /* –°–≤–µ—Ç–ª—ã–π —Ñ–æ–Ω –¥–ª—è —Å–æ–æ–±—â–µ–Ω–∏—è –¥—Ä—É–≥–∞ */
    border-radius: 5px;
    padding: 8px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
    white-space: pre-wrap;
}

/* –ú–∞–ª–µ–Ω—å–∫–∏–π —Ç—Ä–µ—É–≥–æ–ª—å–Ω–∏–∫ —É —Å–æ–æ–±—â–µ–Ω–∏—è –¥—Ä—É–≥–∞ */
.friendText:after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 10px 0 10px 10px;
    border-color: transparent transparent transparent #eee; /* –¶–≤–µ—Ç —Ç—Ä–µ—É–≥–æ–ª—å–Ω–∏–∫–∞, —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏–π —Ñ–æ–Ω—É —Å–æ–æ–±—â–µ–Ω–∏—è */
    left: -10px; /* –†–∞—Å–ø–æ–ª–∞–≥–∞–µ–º —Ç—Ä–µ—É–≥–æ–ª—å–Ω–∏–∫ —Å–ª–µ–≤–∞ –æ—Ç —Å–æ–æ–±—â–µ–Ω–∏—è */
    top: 50%;
    transform: translateY(-50%);
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∏–º–µ–Ω–∏ –¥—Ä—É–≥–∞ */
.friendName {
    font-weight: bold;
    color: #333; /* –¢–µ–º–Ω—ã–π —Ü–≤–µ—Ç –¥–ª—è –∏–º–µ–Ω–∏ –¥—Ä—É–≥–∞ */
    margin-bottom: 5px;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è —Ç–µ–∫—Å—Ç–∞ —Å–æ–æ–±—â–µ–Ω–∏—è –¥—Ä—É–≥–∞ */
.friendMessageText {
    color: #666; /* –°—Ä–µ–¥–Ω–∏–π –æ—Ç—Ç–µ–Ω–æ–∫ —Å–µ—Ä–æ–≥–æ –¥–ª—è —Ç–µ–∫—Å—Ç–∞ —Å–æ–æ–±—â–µ–Ω–∏—è –¥—Ä—É–≥–∞ */
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –≤—Ä–µ–º–µ–Ω–∏ –æ—Ç–ø—Ä–∞–≤–∫–∏ —Å–æ–æ–±—â–µ–Ω–∏—è –¥—Ä—É–≥–∞ */
.friendTime {
    position: absolute;
    bottom: 0px;
    left: 40px; /* –í—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ –≤—Ä–µ–º–µ–Ω–∏ –ø–æ –ª–µ–≤–æ–º—É –∫—Ä–∞—é */
    color: #888;
    font-size: 12px;
    white-space: nowrap;
}
