.input:focus {
    border-color: #2D77A9; /* –∏–ª–∏ –¥—Ä—É–≥–æ–π —Ü–≤–µ—Ç, –∫–æ—Ç–æ—Ä—ã–π –≤—ã —Ö–æ—Ç–∏—Ç–µ */
}

.input {
    padding: 10px 15px;
    border: 1px solid #B0B0B0;
    border-radius: 4px;
    outline: none;
    transition: border-color 0.3s ease-in-out;
    position: relative; /* –¥–æ–±–∞–≤–ª–µ–Ω–æ */
    z-index: 1; /* –¥–æ–±–∞–≤–ª–µ–Ω–æ */
}

.inputWrapper {
    position: relative;
    max-width: 300px; /* –∏–ª–∏ –ª—é–±–æ–π –¥—Ä—É–≥–æ–π —Ä–∞–∑–º–µ—Ä */
}

.superInput {
}

.errorInput {
    border-color: red;
    margin-top: 0px; /* –¥–æ–±–∞–≤–ª–µ–Ω–æ: –æ—Ç—Å—Ç—É–ø, —á—Ç–æ–±—ã –æ—Å–≤–æ–±–æ–¥–∏—Ç—å –º–µ—Å—Ç–æ –¥–ª—è —Å–æ–æ–±—â–µ–Ω–∏—è –æ–± –æ—à–∏–±–∫–µ */
}

.errorInput:focus {
    border-color: darkred;
}

.error {
    position: absolute;
    left: 0;
    bottom: 100%; /* –∏–∑–º–µ–Ω–µ–Ω–æ —Å top –Ω–∞ bottom */
    color: red;
    font-size: 12px;
    margin-bottom: 5px; /* –∏–∑–º–µ–Ω–µ–Ω–æ —Å margin-top –Ω–∞ margin-bottom */
    z-index: 2; /* –¥–æ–±–∞–≤–ª–µ–Ω–æ */
}
