.sendForm {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è textarea */
textarea {
    width: 976px;
    height: 40px;

    border-radius: 20px;
    background: #F5F7FB;
    border: none; /* –£–¥–∞–ª–∏—Ç—å —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—ã–µ —Å—Ç–∏–ª–∏ —Ä–∞–º–∫–∏ */
    padding: 5px 15px; /* –î–æ–±–∞–≤–∏—Ç—å –Ω–µ–º–Ω–æ–≥–æ –≤–Ω—É—Ç—Ä–µ–Ω–Ω–µ–≥–æ –æ—Ç—Å—Ç—É–ø–∞ –¥–ª—è —É–¥–æ–±—Å—Ç–≤–∞ */
    font-family: inherit; /* –ß—Ç–æ–±—ã —à—Ä–∏—Ñ—Ç –±—ã–ª —Ç–∞–∫–∏–º –∂–µ, –∫–∞–∫ –≤ –æ—Å—Ç–∞–ª—å–Ω–æ–π —á–∞—Å—Ç–∏ –≤–∞—à–µ–≥–æ –¥–æ–∫—É–º–µ–Ω—Ç–∞ */
    box-shadow: 0px 1px 2px 0px #1D21261A, 0px 5px 20px 0px #1D212608; /* –û–±—ä–µ–¥–∏–Ω—è–µ–º –¥–≤–∞ –∑–Ω–∞—á–µ–Ω–∏—è box-shadow */
    outline: none; /* –£–±—Ä–∞—Ç—å –≤—ã–¥–µ–ª–µ–Ω–∏–µ –ø—Ä–∏ —Ñ–æ–∫—É—Å–µ */
    resize: none; /* –û—Ç–∫–ª—é—á–∏—Ç—å –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Ä–∞–∑–º–µ—Ä–∞ */
}


.button {
    width: 120px;
    height: 40px;
    border-radius: 20px;
    background: #0066CC;
    font-family: 'Montserrat', sans-serif;  /* –£–±–µ–¥–∏—Ç–µ—Å—å, —á—Ç–æ —à—Ä–∏—Ñ—Ç Montserrat –ø–æ–¥–∫–ª—é—á–µ–Ω –∫ –≤–∞—à–µ–º—É –ø—Ä–æ–µ–∫—Ç—É –∏–ª–∏ –∑–∞–º–µ–Ω–∏—Ç–µ –Ω–∞ –¥–æ—Å—Ç—É–ø–Ω—ã–π */
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
    letter-spacing: 0em;

    border: none;   /* –£–±–∏—Ä–∞–µ–º —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—ã–µ —Ä–∞–º–∫–∏ –∫–Ω–æ–ø–∫–∏ */
    color: white;   /* –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º —Ü–≤–µ—Ç —Ç–µ–∫—Å—Ç–∞ –Ω–∞ –∫–Ω–æ–ø–∫–µ */
    cursor: pointer; /* –ò–∑–º–µ–Ω—è–µ—Ç –∫—É—Ä—Å–æ—Ä –Ω–∞ —Ä—É–∫—É –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */

}

.button:hover {
    background: #0055aa;  /* –ù–µ–º–Ω–æ–≥–æ —Ç–µ–º–Ω–µ–µ –æ—Å–Ω–æ–≤–Ω–æ–≥–æ —Ü–≤–µ—Ç–∞ –¥–ª—è —ç—Ñ—Ñ–µ–∫—Ç–∞ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
}

