.message {
    flex-direction: column;
    padding: 10px;
    margin: 5px 0;
    align-items: flex-end;
    position: relative;  /* –î–æ–±–∞–≤–ª–µ–Ω–æ –¥–ª—è –∞–±—Å–æ–ª—é—Ç–Ω–æ–≥–æ –ø–æ–∑–∏—Ü–∏–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏ */
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è –∏ —Ç–µ–∫—Å—Ç–∞ —Å–æ–æ–±—â–µ–Ω–∏—è */
.imageAndText {
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è –∞–≤–∞—Ç–∞—Ä–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è */
.imageAndText img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    margin-right: 10px;
    object-fit: cover;
    padding: 10px;
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è —Ç–µ–∫—Å—Ç–∞ —Å–æ–æ–±—â–µ–Ω–∏—è –∏ –∏–º–µ–Ω–∏ –æ—Ç–ø—Ä–∞–≤–∏—Ç–µ–ª—è */
.text {
    position: relative;
    max-width: 80%;
    display: flex;
    flex-direction: column;
    background-color: #0066CC;
    border-radius: 5px;
    padding: 8px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
    white-space: pre-wrap;


}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∏–º–µ–Ω–∏ –æ—Ç–ø—Ä–∞–≤–∏—Ç–µ–ª—è */
.name {
    font-weight: bold;
    color: #fff;  /* –ò–∑–º–µ–Ω–∏–ª —Ü–≤–µ—Ç –∏–º–µ–Ω–∏, —á—Ç–æ–±—ã –æ–Ω–æ –±—ã–ª–æ —á–∏—Ç–∞–µ–º—ã–º –Ω–∞ –≥–æ–ª—É–±–æ–º —Ñ–æ–Ω–µ */
    margin-bottom: 5px;
    text-align: right;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è —Ç–µ–∫—Å—Ç–∞ —Å–æ–æ–±—â–µ–Ω–∏—è */
.messageText {
    text-align: right;
    color: white;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –≤—Ä–µ–º–µ–Ω–∏ –æ—Ç–ø—Ä–∞–≤–∫–∏ —Å–æ–æ–±—â–µ–Ω–∏—è */
.time {
    position: absolute;
    bottom: 0px;  /* –í—ã –º–æ–∂–µ—Ç–µ –æ—Ç—Ä–µ–≥—É–ª–∏—Ä–æ–≤–∞—Ç—å —ç—Ç–æ –∑–Ω–∞—á–µ–Ω–∏–µ, —á—Ç–æ–±—ã –ø–æ–ª—É—á–∏—Ç—å –Ω—É–∂–Ω–æ–µ —Ä–∞—Å–ø–æ–ª–æ–∂–µ–Ω–∏–µ –≤—Ä–µ–º–µ–Ω–∏ */
    right: 40px;
    color: #888;
    font-size: 12px;
    white-space: nowrap;
}

