.button {
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
    background: linear-gradient(90deg, #2D77A9, #1F6091);
    color: white;
    outline: none;
}

.button:hover {
    background: linear-gradient(90deg, #1F6091, #2D77A9);
}

.button:active {
    transform: scale(0.95);
}

.button:disabled {
    cursor: not-allowed;
    background: #B0B0B0;
    color: #7A7A7A;
}
.disabled {
    cursor: not-allowed;
    background: #B0B0B0;
    color: #7A7A7A;
}


